<!--  -->
<template>
  <div>
    <div class="list-head">
      <!-- <router-link to="addPopular" class="pry-btn list-head_left">
        <el-button type="primary">添加舆情</el-button>
      </router-link> -->
      <div class="list-head_right">
        <el-button type="info" @click="searchFn">查询</el-button>
        <el-date-picker v-model="endDate" type="date" placeholder="结束时间"></el-date-picker>
        <el-date-picker v-model="startDate" type="date" placeholder="开始时间"></el-date-picker>
      </div>
    </div>
    <el-table :data="tableData" >
      <el-table-column prop="title" label="文章标题" >
        <template scope="scope">
            <a charset="UTF-8" :href="scope.row.url" style="color:red" target="_blank">{{scope.row.title}}</a>
      </template>
      </el-table-column>
      <el-table-column prop="article_index" label="文章ID"></el-table-column>
      <el-table-column prop="source" label="文章来源（zh）"></el-table-column>
      <el-table-column prop="host" label="文章来源（en）"></el-table-column>
      <el-table-column prop="issued_time" label="发表时间"></el-table-column>
      <el-table-column prop="summary" label="文章内容"></el-table-column>
      <el-table-column label="操作" width="230px">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      startDate: "",
      endDate: "",
      title:"",
      article_index:"",
      host:"",
      source:"",
      summary:"",
      issued_time:"",
    };
  },

  methods: {
    handleEdit(row) {
      this.$router.push({name:'添加舆情',
        params:row
      })
    },
    format(value) {
      let date = new Date(value);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      return y + "-" + m + "-" + d;
    },
    searchFn() {
      let params = {
        start: this.format(this.startDate),
        end: this.format(this.endDate)
      };
      this.$x.post(this.url.newsList, params).then(res => {
        this.tableData = res.data;
      });
    }
  },
  mounted() {
    var nowDate = new Date();
    let params = {
      start: this.format(nowDate),
      end: this.format(nowDate)
    };
    console.log(params);
    this.$x.post(this.url.newsList, params).then(res => {
      this.tableData = res.data;
    });
  }
};
</script>
<style>
.pry-btn {
  color: #fff;
}
.list-head {
  display: flex;
}
.list-head_left {
  float: left;
  width: 16%;
}
.list-head_right {
  width: 84%;
  float: right;
  /* display:flex; */
  /* justify-content: space-around; */
}
.list-head_right .el-input,
.list-head_right .el-select,
.list-head_right .el-button {
  width: 120px;
  float: right;
  margin-left: 10px;
}
.list-head_right .el-button {
  width: 80px;
}
</style>

